<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element section">
        <label class="slds-form-element__label settings-label">Start Live Chat With</label>
        <div class="slds-form-element__control picklist-content">
          <input-box
            :current="startLiveChatWith">
          </input-box>
        </div>
      </div>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving" @click="saveSettings">Save</button>
      </footer>
      <dropdown :fixed-value="fixedValue" :options="options" :current="startLiveChatWith" @select="selectStatus">
      </dropdown>
    </div>
  </right-page>
</template>

<script>
import { getActiveAgentStatuses, getCurrentStartLiveChatWithStatus, saveStartLiveChatWithStatus } from '../api/charket-settings'

export default {
  name: 'StartLiveChatSettings',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Start Live Chat Settings'
      },
      options: [],
      startLiveChatWith: {},
      isSaving: false,
      originalStatus: {},
      currentStatus: {},
      fixedValue: { Name: 'Last Status', Id: 'Last Status' }
    }
  },
  computed: {
    hasChanged () {
      return this.originalStatus.Name !== this.startLiveChatWith.Name
    }
  },
  created () {
    this.fetchStartLiveWithStatus()
  },
  methods: {
    async fetchStartLiveWithStatus () {
      try {
        this.$bus.emit('show-loading')
        const [currentStatus, activeStatuses] = await Promise.all([getCurrentStartLiveChatWithStatus(), getActiveAgentStatuses()])
        this.options = activeStatuses
        // Last Status is a fixed value
        if (currentStatus.Status === 'Last Status') {
          this.startLiveChatWith = { Name: 'Last Status', Id: 'Last Status' }
          this.originalStatus = { Name: 'Last Status', Id: 'Last Status' }
        } else {
          this.startLiveChatWith = this.options.find(option => option.Id === currentStatus.Status) || {}
          this.originalStatus = JSON.parse(JSON.stringify(this.startLiveChatWith))
        }
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.startLiveChatWith = JSON.parse(JSON.stringify(this.originalStatus))
    },
    selectStatus (selectedOption) {
      this.startLiveChatWith = selectedOption
    },
    async saveSettings () {
      try {
        this.isSaving = true
        await saveStartLiveChatWithStatus(this.startLiveChatWith.Id)
        this.$bus.emit('success', { content: 'Start Live Chat Settings was saved.', type: 'success' })
        this.showEditModal = false
        this.originalStatus = JSON.parse(JSON.stringify(this.startLiveChatWith))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 30px;
    .settings-label {
      color: #706E6B;
    }
    .picklist-content {
      width: 240px;
    }
  }
</style>
